#applications h2 {
  margin-left: 10%;
  padding-top: 20px;
}

.allAppContainer {
  background-color: white;
  border: 5px solid black;
  display: -ms-flexbox;
  display: flex;
  height: 50vh;
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  overflow: scroll;
  text-align: center;
  width: 80%;
}

.appContainer {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}

.appContainer img {
  border-bottom: 2px solid black;
  height: 100%;
}

.innerAppContainer {
  height: 90%;
  border: 1px solid black;
  margin-left: 10px;
  margin-top: auto;
  margin-bottom: auto;
  position: relative;
}

.midAppContainer {
  background: -webkit-linear-gradient(315deg, rgba(238,238,238,1) 0%,rgba(204,204,204,1) 100%);
  background: -o-linear-gradient(315deg, rgba(238,238,238,1) 0%,rgba(204,204,204,1) 100%);
  background: linear-gradient(135deg, rgba(238,238,238,1) 0%,rgba(204,204,204,1) 100%);
  border-radius: 30px;
  -webkit-box-shadow: 10px 10px 10px rgb(185, 186, 186);
          box-shadow: 10px 10px 10px rgb(185, 186, 186);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  justify-items: center;
  width: 95%;
}

.modal {
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(181, 181, 181, 0.5);
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  width: 100%;
  z-index: 400;
}

.modalInfo {
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 80%;
  -ms-flex-pack: center;
      justify-content: center;
  width: 80%;
}

.modalInfo a {
  color: rgb(66, 158, 250);
}


